/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */

html, body {
    font-family: 'Source Sans Pro', sans-serif;
    margin: 0;
    padding: 0;
    font-size: 100%
}

h2 {
    font-size: 2em;
    margin-bottom: 50px;
}

h3 {
    font-size: 1.4em;
    margin-bottom: 1.2em;
}

p {
    font-size: 1.1em;
}

@media screen and (max-width: 1366px) {
    h2 {
        font-size: 1.5em;
        margin-bottom: 25px;
    }

    h3 {
        font-size: 1.2em;
        margin-bottom: 1em;
    }

    p {
        font-size: 1em;
    }
}

.navbar-default {
    background-color: #1e5eb5;
    border: none;
}
.navbar-default .navbar-text {
    color: #ffffff;
    font-weight: 600;
    letter-spacing: 1px;
}

@media screen and (max-width: 575px) {
    .navbar-default .navbar-text {
        font-size: 14px;
    }
}

.navbar-default .navbar-nav li a {
    color: #ffffff;
    font-weight: 400;
}

.navbar-brand {
    padding: 5px 15px;
}

.navbar-brand img {
    max-width: 50px;
}

@media screen and (max-width: 575px) {
    .navbar-brand img {
        max-width: 40px;
    }
}

.navbar-toggle {
    border-radius: 0;
}

.navbar-default .navbar-toggle:hover {
    background: transparent;
}

.navbar-default .navbar-toggle .icon-bar {
    background: #ffffff;
}

.cover-biokovo {
    background: url("../images/intro-background.jpg") no-repeat;
    background-size: cover;
}

.cover-container {
    margin-left: auto;
    margin-right: auto;
    width: 910px;
}

@media screen and (max-width: 575px) {
    .cover-container {
        margin-left: auto;
        margin-right: auto;
        width: auto;
    }
}

.cover-heading {
    font-size: 4.5em;
    font-weight: 900;
    color: #ffffff;
    letter-spacing: 2px;
    margin-top: -25%;
}

@media screen and (max-width: 1366px) {
    .cover-heading {
        font-size: 3em;
        font-weight: 900;
        color: #ffffff;
        letter-spacing: 2px;
        margin-top: -10%;
    }
}

@media screen and (max-width: 575px) {
    .cover-heading {
        font-size: 2em;
        font-weight: 900;
        color: #ffffff;
        letter-spacing: 2px;
        margin-top: -10%;
    }
}

/* Text */
.u-text-size-14 {
    font-size: 14px;
}

.u-text-size-15 {
    font-size: 15px;
}

.u-text-size-16 {
    font-size: 16px;
}

.u-text-size-18 {
    font-size: 18px;
}

.u-text-size-24 {
    font-size: 24px;
}

.u-text-size-2-5-em {
    font-size: 2.5em;
}

.u-margin-auto {
    margin-left: auto;
    margin-right: auto;
}

.note {
    padding: 15px 0;
    text-indent: -15px;
}

@media screen and (max-width: 575px) {
    .note {
        text-align: center;
    }
}

/* Color */

.u-color-blue {
    color: #1e5eb5;
}

.u-color-blue-light {
    color: #40d0db;
}

.u-color-green {
    color: #2ad69c;
}

.u-color-white {
    color: #ffffff;
}

.u-background-blue {
    background-color: #1e5eb5;
}

.u-background-gray {
    background-color: #f6f6f6;
}

.adult {
    border-bottom: 3px solid #40d0db;
    border-right: 3px solid #d7d7d7;
    padding: 2em 0 1em 0;
}

.children {
    border-bottom: 3px solid #2ad69c;
    padding: 2em 0 1em 0;
}

.trip-details {
    padding: 2em 0;
}

.trip-details p {
    font-size: 1.1em;
}

@media screen and (max-width: 575px) {
    .adult {
        border-bottom: 3px solid #40d0db;
        border-right: none;
        padding: 0.5em;
    }

    .children {
        border-bottom: 3px solid #2ad69c;
        padding: 0.5em;
    }

    .children p,
    .adult p {
        font-size: 1.2em;
    }

    .trip-details p {
        font-size: 1em;
    }
}

.u-text-weight-600 {
    font-weight: 600;
}

.u-text-weight-900 {
    font-weight: 900;
}

.uppercase {
    text-transform: uppercase;
}

.section-title {
    font-weight: 700;
}

.section-title:after {
    display: inline-block;
}

.section-subtitle {
    font-weight: 600;
    margin-top: 0;
}

.adventure-title {
    margin-bottom: 50px;
}

@media screen and (max-width: 1366px) {
    .adventure-title {
        margin-bottom: 25px;
    }
}

.o-media {
    min-height: 100px;
    margin-bottom: 5em;
}

@media screen and (max-width: 1366px) {
    .o-media {
        min-height: 100px;
        margin-bottom: 2.5em;
    }
}

.o-media__img-l {
    float: left;
    margin-right: 30px;
}

.o-media__body-l {
    margin: 0 0 0 120px;
}

.o-media__img-r {
    float: right;
    margin-left: 30px;
}

.o-media__body-r {
    text-align: right;
    margin: 0 120px 0 0;
}

@media screen and (max-width: 767px) {
    .o-media__body-l,
    .o-media__body-r {
        margin: 0;
    }

    .o-media__body-r {
        text-align: left;
    }
}

/* hr */
.e-hr--fix {
    max-width: 10em;
}

.e-hr--fix {
    border-width: 6px;
}

.e-hr--blue {
    border-color: #1e5eb5;
}

.contact-left {
    padding: 1em 5em 0 5em;
}

@media screen and (max-width: 575px) {
    .contact-left {
        padding: 1em 1em 6em 1em;
    }
}

.contact-left h2 {
    font-weight: 600;
}

label {
    font-weight: 400;
}

.btn-default {
    background: #00c2a8;
    border: none;
    padding-left: 3em;
    padding-right: 3em;

}

.form-control, .btn {
    border: none;
    border-radius: 0;
}

/* Owl */
.owl-carousel .owl-nav .owl-prev,
.owl-carousel .owl-nav .owl-next,
.owl-carousel .owl-dot {
    /*width, height*/
    width: 65px;
    height: 65px;
}

.owl-carousel .owl-nav .owl-prev,
.owl-carousel .owl-nav .owl-prev:hover {
    background: url('../images/icons/arrow-left.png') no-repeat;
    position: absolute;
    top: 40%;
    left: 0px;
    display: block !important;
    border: 0px solid black;
}

.owl-carousel .owl-nav .owl-next,
.owl-carousel .owl-nav .owl-next:hover {
    background: url('../images/icons/arrow-right.png') no-repeat;
    position: absolute;
    top: 40%;
    right: 0px;
    display: block !important;
    border: 0px solid black;
}

.contact-right {
    padding: 0;
    background: #ffffff;
}

iframe {
    width: 100%;
    display: block;
}

.copyright {
    padding-top: 15px;
    padding-bottom: 10px;
}

.copyright p {
    letter-spacing: 1px;
}
